.sidebar {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  width: 300px;
  border-radius: 3px;
  background-color: rgba(167, 200, 163, 0.5);
}

.sidebar__bottom {
  overflow-x: hidden;
  overflow-y: scroll;
  padding: 0 10px;
  max-height: 300px;
  border-top: 1px solid rgba(0, 0, 0, 0.5);
}

.stats {
  flex: 1;
}

.stats__row {
  display: flex;
  justify-content: space-around;
}

.player {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  width: 130px;
  border-radius: 3px;
  background-color: #ffffff;
  color: #222222;
  font-size: 18px;
}

.player__head {
  padding: 3px 10px 2px;
  border-bottom: 3px solid rgba(86, 150, 77, 0.9);
  background-color: #ece9d8;
}

.player__body {
  display: flex;
  flex-direction: column;
  padding: 3px 10px;
  flex: 2 auto;
}

.player__score {
  display: flex;
  flex-direction: column;
  text-align: right;
  flex: 2 auto;
  justify-content: flex-end;
}

.player__score-line {
  position: relative;
  padding: 2px 0;
  font-weight: 700;
}

.player__score-line:last-child:before {
  right: 0;
  left: auto;
  display: none;
  width: 70%;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.2);
}

.player__score-line.plus {
  color: #6da86b;
}

.player__score-line.minus {
  color: #a8645c;
}

.table-m {
  border-collapse: collapse;
  font-size: 20px;
}

.table-m td {
  padding: 7px;
}

.table-m td:nth-child(2) {
  text-align: right;
}

.table-m tfoot {
  border-top: 2px solid rgba(255, 255, 255, 0.3);
  text-align: right;
}

.table-m tfoot td {
  padding-top: 10px;
}
